body {
    height: 100vh;
    color: #1f1f1f;
    text-align: justify;
}

table {
    border-collapse: collapse;
    width: 100%;
}

th,
td {
    text-align: center;
    padding: 8px;
}

tr:nth-child(even) {
    background-color: #f2f2f2
}

td:first-child {
    border-right: solid #37C0FB 1px;
}

th {
    background-color: #37C0FB;
    color: white;
    font-weight: bold;
}

input{
    margin: 1em auto;
}

.boton:hover {
    background: #1F1F1F;
}

a:hover {
    color: #1F1F1F;
}

.lista {
    list-style-type: circle;
}

.centrar {
    margin: 0 auto;
}

.centrarcontent {
    text-align: center;
}

.pic {
    width: 70%;
    margin: 2em auto !important;
}

.imtitulo  {
    width: 9%;
    display: inline-block;
}

.check {
    width: 4%;
    margin-left: 0.8em;
    padding-top: 0.3em;
}

.miniatura {
    width: 30%;
}

.miniatura:hover {
    width: 36%;
}

.margen {
    margin-right: 1em;
}

.icon.major {
    cursor: pointer;
}

.icon.major:hover {
    background: #1F1F1F;
}

.animar {
    animation: animar 5s 1;
    -moz-animation: animar 5s 1;
    -webkit-animation: animar 5s 1;
    -o-animation: animar 5s 1;
    border-radius: 5px;
    -webkit-border-radius: 5px;
}

@keyframes animar {
    0% {
        transform: rotate(0deg);
        left: 0px;
    }

    25% {
        transform: rotate(20deg);
        left: 0px;
    }

    50% {
        transform: rotate(0deg);
        left: 300px;
    }

    55% {
        transform: rotate(0deg);
        left: 300px;
    }

    70% {
        transform: rotate(0deg);
        left: 300px;
    }

    100% {
        transform: rotate(-360deg);
        left: 0px;
    }
}

$color-blue: #83af9b;
$color-green: #c8c8a9;
$color-brown: #774f38;
$color-beige: #ece5ce;
$color-yellow: #f9cdad;
$color-pink: #fe4365;

#homepage {
    height: 100vh;
    margin: 0;
    display: grid;
    place-items: center;
    font: 2vw system-ui;
    background-color: $color-blue;
}

#homepage p {
    transform: translatey(0px);
    animation: float 5s ease-in-out infinite;
    mix-blend-mode: multiply;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 3px;
    font-size: 15px;
    color: $color-brown;
    background-color: $color-beige;
    padding: 50px;
    border-radius: 11px;
    position: relative;
    box-shadow: 20px 20px $color-blue;
    font-family: "Baloo 2", cursive;
}

#homepage p:after {
    transform: translatey(0px);
    animation: float2 5s ease-in-out infinite;
    content: ".";
    font-weight: bold;
    text-shadow: 22px 22px $color-blue;
    text-align: left;
    font-size: 55px;
    width: 55px;
    height: 11px;
    line-height: 30px;
    border-radius: 11px;
    background-color: $color-beige;
    position: absolute;
    display: block;
    bottom: -30px;
    left: 0;
    box-shadow: 22px 22px $color-blue;
    z-index: -2;
}

@keyframes float {
    0% {
        transform: translatey(0px);
    }

    50% {
        transform: translatey(-20px);
    }

    100% {
        transform: translatey(0px);
    }
}

@keyframes float2 {
    0% {
        line-height: 30px;
        transform: translatey(0px);
    }

    55% {
        transform: translatey(-20px);
    }

    60% {
        line-height: 10px;
    }

    100% {
        line-height: 30px;
        transform: translatey(0px);
    }
}

#imlogo {
